import React from "react"
import { compose, withProps } from "recompose"
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"

const Pos = {lat: 37.2768373, lng: 127.01586339999994}

const MyGoogleMap = compose (
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?key=AIzaSyAkkBUApToPp6AQd6hUTEfdzp_gfh20UGc&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withScriptjs,
  withGoogleMap
)((props) =>
  <GoogleMap
    defaultZoom={17}
    defaultCenter={Pos}
  >
    {props.isMarkerShown && <Marker position={Pos} onClick={props.onMarkerClick} />}
  </GoogleMap>
)

class MyMapComponent extends React.PureComponent {
  state = {
    isMarkerShown: false,
  }

  componentDidMount() {
    this.delayedShowMarker()
  }

  delayedShowMarker = () => {
    setTimeout(() => {
      this.setState({ isMarkerShown: true })
    }, 1000)
  }

  render() {
    return (
      <MyGoogleMap
        isMarkerShown={this.state.isMarkerShown}
      >
      </MyGoogleMap>
    )
  }
}

export default MyMapComponent



